#GameOverPrompt{
    height: 200px;
    width: 400px;
    
    background-color: rgb(126, 132, 136);
    color: rgb(255, 255, 254);
    border-style: solid;
    position: fixed;    
    top: 50px;
    left: 0;
    right: 0;
    margin: 0 auto;
}

.gameOverItems{
    height: fit-content;
    width: fit-content;
    margin: 5px auto 20px auto;
}

#continue{
    display: block;
   height: 30px;
   width: fit-content;
   margin: auto auto 15px auto;
   color: rgb(255, 255, 255);
   cursor: pointer;
}